import data from './config/list'
export default {
	data() {
		return {
			data,
			url: 'http://127.0.0.1:8888/studentCourse',
			parentId: uni.getStorageSync('ID')
		};
	},
	onShow() {
		this.getList()
	},
	methods: {
		getList() {
			let that = this
			// console.log(this.data);
			uni.request({
				url: this.url,
				data: {
					id: that.parentId
				},
				success(res) {
					console.log(res.data.data)
					that.data = res.data.data
				}
			})
		},
		handleCall(phone) {
			uni.showModal({
			    title: '提示',
			    content: '确定拨打老师电话',
					confirmText: '拨打',
					confirmColor: '#ff0000',
			    success: function (res) {
			        if (res.confirm) {
			            uni.makePhoneCall({
			            	phoneNumber: phone
			            })
			        }
			    }
			})
		}
	}
}
